@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Tangerine&display=swap);
html{
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*WHOLE CONTAINER OF INFOSECTION AND FORM SECTION*/

.myContent {
  height: 100vh;
  width: 100%;
}

/*INFO SECTION TAKES 50% of the entire height*/

.infoSection {
  height: 50%;
  width: 100%;
}

/*FormSection Takes The 50% of the entire height*/

.formSection {
  height: 50%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Nunito";
}

/*PARENT is InfoSection, this takes 20% of the parent's height*/

.infoIntroduction {
  height: 15%;
  font-size: 3em;
  font-family: "Nunito";
}

/*PARENT is InfoSection, this takes 80% of the parent's height*/

.infoGrid {
    width: 100%;
  height: 80%;
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  align-items: center;
}

/*Parent is infoGrid, this is a single Card*/

.myCard {
  background-color: white;
  height: 85%;
  min-height: 300px;
  width: 28%;
  display: flex;
  justify-content: center;
  font-family: "Nunito";
}

.cardContent {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  width: 85%;
  margin-left: 20%;
  height: 100%;
  text-align: left;
}

.myCard .myImage {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.myCard .myInfo {
  font-size: 1.2em;
}

.myCard .myDetails a {
  background-color:#6e0d2c;
  color: white;
  text-decoration: none;
  font-weight: bold;
  padding: 0.8em;
}

.myCard .myQuestion {
  font-size: 1.8em;
  font-weight: bold;
  color: rgb(34, 31, 31);
}

/*FORM STYLING*/

.myForm {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  text-align: left;
  width: 75%;
  height: 90%;
  background-color: white;
  border-radius: 6px;
}

.formQuestion {
  width: 100%;
  height: 25%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  background-color: #6e0d2c;
  color: white;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.formMargin {
  margin-left: 10%;
}

.formQuestion span {
  font-size: 2.5em;
  font-weight: bold;
}
.formQuestion small {
  font-size: 1em;
  color: rgb(230, 230, 230);
}

.formRow {
  height: 25%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  width: 80%;
}

.formRow label {
  width: 100%;
  display: flex;
  justify-content: start;
  font-size: 1.3em;
  font-weight: bold;
  color: black;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.formRow input {
  width: 100%;
  height: 30%;
}

.formRowButton {
  height: 25%;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-flow: column;
}

.myTextBox {
  width: 80%;
  height: 100%;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 2px solid #6e0d2c;
  outline: none;
  background-color: transparent;
  color: #6e0d2c;
}

.myTextBox::-webkit-input-placeholder {
  color: #6e0d2c;
}

.myTextBox:-ms-input-placeholder {
  color: #6e0d2c;
}

.myTextBox::-ms-input-placeholder {
  color: #6e0d2c;
}

.myTextBox::placeholder {
  color: #6e0d2c;
}

.redPlaceHolder::-webkit-input-placeholder {
  color: red;
}

.redPlaceHolder:-ms-input-placeholder {
  color: red;
}

.redPlaceHolder::-ms-input-placeholder {
  color: red;
}

.redPlaceHolder::placeholder {
  color: red;
}

.goButton {
  background-color: #6e0d2c;
  border: none;
  padding: 1em;
  color: white;
  outline: none;
  font-weight: bold;
  font-size: 1.3em;
}

.goButton:active {
  background-color: white;
  border: 0.5px solid black;
  color: #6e0d2c;
}

@media screen and (max-width: 992px) {
  /*INFO SECTION*/
  .myContent {
    height: auto;
  }

  .infoGrid {
    display: flex;
    flex-flow: column;
    height: auto;
    width: 100%;
    margin: 1em 0 1em 0;
  }

  .formSection {
    height: auto;
  }

  .myCard {
    width: 100%;
    height: 400px;
    display: flex;
    background-color: rgb(250, 250, 250);
    margin: 10px 0 10px 0;
  }

  .myCard .myQuestion {
    font-size: 2.5em;
  }

  .myCard .cardContent {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    margin-left: 0;
  }

  .myCard .myInfo {
    font-size: 1.4em;
  }

  /*FORM SECTION*/

  .myForm {
    width: 100%;
    height: 100%;
  }

  .formSection {
    height: auto;
  }
  .formQuestion {
    height: auto;
    padding: 30px 0 30px 0;
  }
  .formRow {
    height: 130px;
  }

  .formRowButton {
    padding: 10px 0 30px 0;
  }
}
@media screen and (max-height: 800px) {
  /*INFO SECTION*/
  .myContent {
    height: auto;
  }
  .formRow {
    height: 130px;
  }

  .myCard{
    height: 400px;
    margin: 5% 0 5% 0;
  }
  .formQuestion {
    height: auto;
    padding: 30px 0 30px 0;
  }
}

.welcomeText{
    font-family: 'Great Vibes';
    font-size: 45px;
    color:white;
    text-shadow: 2px 2px black;
    position: absolute;
    bottom: 30%;
}


.textContainer{
        color: white;
        font-family: "Tangerine";
        z-index: inherit;
        -webkit-user-select:none;
            -ms-user-select:none;
                user-select:none;
        text-align:left;
        position: relative;
        width: 90%;
        height: 90%;
        display: flex;
        flex-flow: column;
        align-items: flex-start;
        justify-content:center;
        margin-bottom: 100px;
}

.textWrapper{
    z-index: +1;
    position: absolute;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 600px;
}

.bigText{
    line-height: .8em;
    font-size:10vw;
    margin-left: 5%;

}

.smallText{
    position: relative;
    font-size:4vw;
    margin-left: 5%;

}



@media screen and (max-width:1380px){

    .textContainer{
        justify-content:flex-end;
    }
    .bigText{
        line-height: .8em;
        font-size:14vw;
        margin-left: 5%;
    }
    
    .smallText{
        font-size:6vw;
        margin-left: 5%;
    }
    
}

@media screen and (max-width:500px){
    .bigText{
        line-height: .8em;
        font-size:18vw;
        margin-left: 5%;
    }
    
    .smallText{
        font-size:8vw;
        margin-left: 5%;
    }
}



@-webkit-keyframes updown {
    0%{
        bottom:20px;
    }

    50%{
        bottom:30px;
    }

    100%{
        bottom:20px;
    }
}



@keyframes updown {
    0%{
        bottom:20px;
    }

    50%{
        bottom:30px;
    }

    100%{
        bottom:20px;
    }
}


.bi-chevron-compact-down{
    -webkit-animation: updown infinite ease-in-out 2s;
            animation: updown infinite ease-in-out 2s;
    position:absolute;
}

.imgSectionWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

 .myPrivate{
